document.addEventListener("DOMContentLoaded", function () {
    iniciarApp();
});

function iniciarApp() {
    navegacionFija();
    crearGaleria();
    scrollNav();
}

function navegacionFija(){
    const barra = document.querySelector(".header");
    const sobreFestival = document.querySelector(".about-festival");
    const body = document.querySelector("body");

    window.addEventListener("scroll", function(){
        if(sobreFestival.getBoundingClientRect().bottom < 0){
            barra.classList.add("fixed");
            body.classList.add("body-scroll");
        }else{
            barra.classList.remove("fixed");
            body.classList.remove("body-scroll");
        }
    })
}

function scrollNav() {
    const enlaces = document.querySelectorAll(".main-navegation a");
    enlaces.forEach( enlace => {
        enlace.addEventListener('click', function(e) {
            e.preventDefault();

            const seccionScroll = e.target.attributes.href.value;
            const seccion = document.querySelector(seccionScroll);
            seccion.scrollIntoView({ behavior: "smooth"});
        });
    });
}

function crearGaleria() {
    const galery = document.querySelector(".img-galery");

    for (i = 1; i <= 12; i++) {
        const image = document.createElement("picture");

        image.innerHTML = `
            <source srcset="build/img/thumb/${i}.avif" type="image/avif" onclick="mostrarImagen(${i})">
            <source srcset="build/img/thumb/${i}.webp" type="image/webp" onclick="mostrarImagen(${i})">

            <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg"
                alt="Imagen galería" onclick="mostrarImagen(${i})">
            <!--Aquí siempre debemos colocar un img con un src con la version que mas es soprtada, si el navegador soporta cualquiera de las otras dos que son mas livianas va a colocar esa y se recomienda poner ese width y ese height para mejorar el rendimiento y luego ponerle width 100% y height auto en globales para que quede bien-->
        `
        galery.appendChild(image);
    }

}

function mostrarImagen(id) {
    const image = document.createElement("picture");

    image.innerHTML = `
            <source srcset="build/img/grande/${id}.avif" type="image/avif">
            <source srcset="build/img/grande/${id}.webp" type="image/webp">

            <img loading="lazy" width="200" height="300" src="build/img/grande/${id}.jpg"
                alt="Imagen galería">
            <!--Aquí siempre debemos colocar un img con un src con la version que mas es soprtada, si el navegador soporta cualquiera de las otras dos que son mas livianas va a colocar esa y se recomienda poner ese width y ese height para mejorar el rendimiento y luego ponerle width 100% y height auto en globales para que quede bien-->
    `
    //Crea el overlay con la imagen
    const overlay = document.createElement("DIV");
    overlay.appendChild(image);
    overlay.classList.add("overlay");
    overlay.onclick = function () {
        overlay.remove()
        body.classList.remove("pinup-body");
    }
    //Boton para cerrar el modal
    const cerrarModal = document.createElement("P");
    cerrarModal.textContent = "X";
    cerrarModal.classList.add("btn-close");
    cerrarModal.onclick = function () {
        overlay.remove()
        body.classList.remove("pinup-body");
    }
    overlay.appendChild(cerrarModal);

    //Lo pone en el HTML
    const body = document.querySelector("body");
    body.appendChild(overlay);
    body.classList.add("pinup-body");
}